@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
.container {
  width: 80%;
  margin: 0 auto;
  padding: 0;
}

@media (max-width: 445px) {
  .container {
    min-width: 300px;
  }
}

.off {
  display: none;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 11;
  background-color: #0f152e;
}

header.gnbOn {
  background-color: #fff;
}

header .container .row {
  height: 100px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

header .container .row h1 {
  margin: 0 !important;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

header .container .row .logo {
  height: 30px;
}

header .container .row .xi-bars, header .container .row .xi-close {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 50px;
  cursor: pointer;
  color: #fff;
}

header .container .row .xi-close {
  color: #333;
}

header #gnb {
  display: none;
  position: absolute;
  top: 100px;
  height: 580px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.9);
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

header #gnb ul {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

header #gnb ul li {
  font-size: 48px;
}
