@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
.container {
  width: 80%;
  margin: 0 auto;
  padding: 0;
}

@media (max-width: 445px) {
  .container {
    min-width: 300px;
  }
}

.off {
  display: none;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

#mainBanner {
  height: 600px;
  background-color: #0f152e;
}

#mainBanner .row {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

#mainBanner .row .textArea {
  padding: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: #fff;
}

#mainBanner .row img {
  width: auto;
  height: 500px;
}

#main #itemList {
  text-align: center;
  padding: 100px 0;
}

#main #itemList h2 {
  margin-bottom: 30px;
}

#main #itemList p {
  margin-bottom: 80px;
}

#main #itemList #listSlide {
  position: relative;
}

#main #itemList #listSlide .container {
  overflow: hidden;
}

@media (max-width: 991.9px) {
  #main #itemList #listSlide .swiper-wrapper {
    display: block;
  }
}

#main #itemList #listSlide .swiper-slide {
  cursor: pointer;
  height: 300px;
  -webkit-box-sizing: border-box !important;
          box-sizing: border-box !important;
  text-align: right;
  -webkit-box-shadow: 1px 1px 0 #666 inset, -1px -1px 0 #666 inset;
          box-shadow: 1px 1px 0 #666 inset, -1px -1px 0 #666 inset;
  padding: 50px 30px;
}

@media (max-width: 991.9px) {
  #main #itemList #listSlide .swiper-slide {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-bottom: 30px;
    padding: 20px;
  }
}

#main #itemList #listSlide .swiper-slide i {
  font-style: normal;
  font-size: 16px;
  color: #666;
}

@media (max-width: 445px) {
  #main #itemList #listSlide .swiper-slide i {
    font-size: 12px;
  }
}

#main #itemList #listSlide .swiper-slide h3 {
  margin-top: 10px;
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 15px;
}

#main #itemList #listSlide .swiper-slide .row {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}

#main #itemList #listSlide .swiper-slide .row img {
  width: auto;
  height: 120px;
}

#main #itemList #listSlide .swiper-slide .row .listNum {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  font-size: 68px;
  font-weight: bold;
  margin: 0;
}

#main #itemList #listSlide .swiper-slide:hover {
  -webkit-box-shadow: 10px 10px 0 #ec6718 inset, -10px -10px 0 #ec6718 inset;
          box-shadow: 10px 10px 0 #ec6718 inset, -10px -10px 0 #ec6718 inset;
  padding: 50PX 30PX;
}

#main #itemList #listSlide .swiper-slide:hover .listNum {
  color: #ec6718;
}

#main #itemList #listSlide .swiper-button-prev, #main #itemList #listSlide .swiper-button-next {
  width: 70px;
  height: 70px;
  margin: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-position: center;
  background-size: cover;
}

#main #itemList #listSlide .swiper-button-prev::before, #main #itemList #listSlide .swiper-button-prev:after, #main #itemList #listSlide .swiper-button-next::before, #main #itemList #listSlide .swiper-button-next:after {
  display: none;
}

@media (max-width: 991.9px) {
  #main #itemList #listSlide .swiper-button-prev, #main #itemList #listSlide .swiper-button-next {
    display: none;
  }
}

#main #itemList #listSlide .swiper-button-prev {
  left: 3% !important;
}

#main #itemList #listSlide .swiper-button-next {
  right: 3% !important;
}

#main #request {
  background-color: #0f152e;
  height: 200px;
}

#main #request .container {
  position: relative;
  height: 100%;
}

#main #request .container .row {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

#main #request .container .row h2 {
  display: block;
  font-size: 32px;
  color: #fff;
  padding: 0;
  word-break: keep-all;
}

#main #request .container .row h2.responsive {
  position: absolute;
  top: 15px;
  padding-left: 35px;
}

#main #request .container .row form {
  padding: 0;
}

#main #request .container .row form .responsive {
  width: 100%;
}

#main #request .container .row form input[type='text'] {
  padding: 5px;
  border: 3px solid #999;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

#main #request .container .row form input[type='text'].responsive {
  width: 30% !important;
}

#main #request .container .row form label {
  color: #999;
  font-size: 15px;
  margin-left: 5px;
}

#main #request .container .row form input[type='submit'] {
  border: 10px solid #ec6718;
  background-color: #fff;
  color: #ec6718;
  font-size: 32px;
  font-weight: 600;
  height: 200px;
  margin: 0;
}

#main #request .container span {
  position: absolute;
  bottom: 30px;
  color: #fff;
  width: 100%;
  text-align: center;
  font-size: 14px;
}

#main #request .container span.responsive {
  bottom: 60px;
}

#main #request.tablet form > div > input {
  margin-top: 70px;
  margin-bottom: 40px;
}

#main #request.tablet form > div div {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

#main #request.tablet form > div > .responsive {
  bottom: 45px;
}

#main #request.tablet form > div span {
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  left: 10px;
}

#main #request.mobile {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 20px 0 0;
  position: fixed;
  bottom: 70px;
  z-index: 3;
}

#main #request.mobile h2 {
  width: 100%;
  position: relative;
  margin-bottom: 20px;
}

#main #request.mobile h2 .xi-close {
  position: absolute;
  display: block !important;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: #fff;
  font-size: 30px;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
}

#main #request.mobile form > div {
  width: 100%;
  padding: 0;
  -webkit-box-pack: justify !important;
      -ms-flex-pack: justify !important;
          justify-content: space-between !important;
  margin-top: 0 !important;
}

#main #request.mobile form > div > input {
  width: 30%;
  margin-bottom: 40px;
}

#main #request.mobile form > div > div {
  width: 100%;
  padding: 0;
}

#main #request.mobile form > div span {
  display: block;
  bottom: 30px !important;
  padding: 0;
  text-align: left;
}

#main #request.mobile input[type='submit'] {
  display: none;
}

#main #mobile {
  width: 100%;
  height: 70px;
  position: fixed;
  z-index: 3;
  bottom: 0;
  background-color: #0f152e;
}

#main #mobile .container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

#main #mobile .container > input {
  width: 100%;
  height: 45px;
  border-radius: 5px;
  border: none;
}
